/* ==== START VARIABLES ==== */
:root {
  /* Colors */
  --first-color: #ffbf00;
  --second-color: #051e34;
  --light-color: #f6f7f9;
  --white-color: #fff;
  /* Fonts */
  --big-font-size: 3rem;
  --h2-font-size: 2rem;
  --normal-font-size: 1rem;
  /* Other */
  --radius: 0.5rem;
  --shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
  --transition: 0.5s ease-in-out;
}

@media (max-width: 1200px) {
  :root {
    /* Fonts */
    --big-font-size: 2rem;
    --h2-font-size: 1.25rem;
    --normal-font-size: 0.938rem;
  }
}

  

/* ==== GLOBAL RULES ==== */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
  font-size: var(--normal-font-size);
  background-color: #292a29 !important;
}

.container {

  padding-left: 15px;
  padding-right: 15px;
  margin-right: auto;
  margin-left: auto;
}

/* ==== SMALL ==== */
